import React from "react"

import Container from '../components/container'
import Head from '../components/head'
import Img from 'gatsby-image'
import { graphql } from 'gatsby'
import Fade from 'react-reveal/Fade'

import '../styles/index.css'

const IndexPage = ({ data }) => {

    return (
        <div>
            <Container >
                <Head title="Home"/>

                <Fade>
                    <div className="row_hero">
                        <div className="ctaCont">

                            <h2>
                                Work is love,<br />
                                made visible.
                            </h2>

                            <h4>
                                Android Developer at <a href="https://in.bookmyshow.com/" target="_blank" rel="noopener noreferrer">BookMyShow</a>.
                                A Google-certified <a href="https://credential.net/xgjuoa5t" target="_blank" rel="noopener noreferrer">Associate Android Developer</a> and <a href="https://confirm.udacity.com/GJPAAZSG" target="_blank" rel="noopener noreferrer">
                                Android Developer Nanodegree</a> recipient.
                            </h4>

                            <a href="mailto:jainakshansh@outlook.com" rel="noopener noreferrer">
                                <button className="button">GET IN TOUCH</button>
                            </a>

                        </div>

                        <div className="imgCont">
                            {!!data.landing && !!data.landing.childImageSharp
                                ? <Img fluid={data.landing.childImageSharp.fluid} alt={"BMS Mockup"} />
                                : <img src={data.landing.publicURL} alt={"BMS Mockup"} />
                            }
                        </div>
                    </div>
                    
                </Fade>
                
            </Container>
        </div>
    )
}

export default IndexPage


export const projectImages = graphql`
    query ProjectImages {
        landing: file (relativePath: {eq: "bms.png" }) {
            childImageSharp {
                fluid(maxWidth: 2048) {
                    ...GatsbyImageSharpFluid
                }
            }
        }
    }
`